.post-heading {
    text-align: left;
    padding: 0.5rem 0 0.2rem 0.5rem;
    border-style: none none solid none;
    border-color: #2c384e;
    border-width: 2px;
    height: 100%;
}

.post-container{
    padding-bottom: 1rem;
}

.loading-box {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 45rem;
    padding-top: 1rem;
}


.post-card {
    /* width: 18rem;
    margin: 0.2rem; */
    color: white;
}

.post-image{
    height: 8rem;
    object-fit: cover;
}

@media screen and (max-width: 768px) {
    .post-image{
        height: 12rem;
    }
}