.projects-box {
    overflow-y: scroll;
    max-height: 83vh;
}

.project-container{
    padding-bottom: 0;
}

.projects-heading {
    text-align: left;
    padding: 0.5rem 0 0.2rem 0.5rem;
    border-style: none none solid none;
    border-color: #2c384e;
    border-width: 2px;
}

.loading-box {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 45rem;
    padding-top: 1rem;
}


.project-card {
    /* width: 18rem;
    margin: 0.2rem; */
    color: white;
}

@media screen and (max-width: 768px) {
    .projects-box {
        height: inherit;
        overflow-y: visible;
    }
}